th {
  font-weight: bold;
}

.widget-forms {
  .sortable {
    cursor: pointer;
    font-weight: bold;
    margin-left: -5px;
    &:hover {
      &:after {
        content: '\f0dc';
      }
    }
    &:after {
      font-family: 'FontAwesome';
      margin-left: 5px;
      position: absolute;
    }
    &.sorted-asc:after {
      content: "\f0de";
    }
    &.sorted-desc:after {
      content: "\f0dd";
    }
  }
  header {
    display: flex;
    div {
      flex: 1;
    }
    div:first-of-type {
      flex: 2;
    }
  }

  .action {
    display: flex;
  }

  .form-row-actions a {
    cursor: pointer;

    &.delete {
      color: #FF4136;
    }
  }

  > div > main.content {
    /*max-height: 400px;*/
  }
}

.forms-collection {
  & > div {
    padding: 10px;
  }
  & > div:nth-child(odd){
    background-color: #EBEDEE;
  }
  .deleting {
    color: #FF4136;
    background-color: #ECEDEE;
    a {
      color: #FF4136;
    }
    footer {
      display: none;
    }
  }
  main {
    display: flex;
    div {
      flex: 1;
    }
    div:first-of-type {
      flex: 2;
    }
  }
  footer ul {
    margin: 0;
    padding-top: 20px;
    display: flex;
    li {
      a.delete {
        color: red;
      }
      &:after {
        content: ' |';
        margin: auto 5px;
      }
      &:last-child:after {
        content: '';
      }
    }
  }
}

.nf-template-grid {
  .section-head {
    font-size: 36px;
    font-weight: bold;
    width: 75%;
    display: block;
    border-bottom: 1px solid #ccc;
    padding-bottom: 15px;
    margin: 60px 0px 25px 10px;
  }

  .template-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    & > div {
      display: flex;
      width: 50%;
    }

    .template {
      display: grid;
      width: 100%;
      a {
        color: #424242;
        display: block;
        margin: 5px;
        padding: 30px;
        border: 1px solid #ccc;
        border-radius: 4px;
        min-height: 80px;
        cursor: pointer;

        &:hover {
          background: #1EA9EA;
          color: #fff;
        }

        .title {
          display: block;
          font-size: 20px;
          margin-bottom: 10px
        }
      }
    }

    .ad {
      a {
        border-color: #EBEDEE;
      }
    }

    .default {
      a {
        background: #EBEDEE;
        border-color: #EBEDEE;
      }
    }
  }
}

table .forms-table-row li {
    display: inline-block;
}

.modal-template {
  padding: 20px;
  img {
    width: 100%;
  }
  iframe {
    width: 100%;
    height: 214px;
  }
  p:first-of-type {
    font-size: 16px;
    font-weight: bold;
    line-height: 20px;
    padding: 16px 40px;
    text-align: center;
  }
  .actions {
    margin-top: 30px;
    &::after {
      display: block;
      clear: both;
      content: "";
    }
  }
  .primary.nf-button {
    float: right;
  }
}
